import {LOGIN, LOGOUT} from './actionNames'

const initUser = {
    isLoggedIn: false,
    id: null,
    name: null,
    role: null,
    token: null
}

const authReducer = (state = initUser, action)=>{
    switch (action.type){
        case LOGIN:
            const newState = {
                isLoggedIn: true,
                token: action.payload.token,
                role: action.payload.role,
                id: action.payload.id,
                fname: action.payload.fname,
                lname: action.payload.lname
            }
            return newState
        case LOGOUT:
            fetch('http://localhost:1000/auth/logout',{
                method:'POST',
                headers:{'Content-Type':'application/json'},
                body:JSON.stringify({
                    token:state.token
                })
            })
            return initUser
        default:
            return state
    }
}

export default authReducer